class Editor extends React.Component {

  render() {
    return (
      <Form>
        <TextArea
          id="editor"
          rows={10}
          onChange={(event, data) => this.props.onUpdate(data.value)}
          value={this.props.markdownText}
        />
      </Form>
    )
  }
}

class Preview extends React.Component {

  createMarkup(htmlText) {
    return { __html: htmlText };
  }

  render() {
    marked.setOptions({
      gfm: true,
      breaks: true,
    });

    const htmlText = marked(this.props.markdownText);

    return <div style={{ padding: "10px" }} id="preview" dangerouslySetInnerHTML={this.createMarkup(htmlText)} />;
  }
}

export default class App extends React.Component {

  constructor(props) {
    super(props);
    this.switchToEditorTab = this.switchToEditorTab.bind(this);
    this.switchToPreviewTab = this.switchToPreviewTab.bind(this);
    this.updateMarkdownText = this.updateMarkdownText.bind(this);

    this.state = {
      activeTab: "editor",
      markdownText: sampleMarkdownText
    }
  }

  switchToEditorTab() {
    this.setState({
      activeTab: "editor"
    })
  }

  switchToPreviewTab() {
    this.setState({
      activeTab: "preview"
    })
  }

  updateMarkdownText(text) {
    console.log("text:", text)
    this.setState({
      markdownText: text
    })
  }

  render() {
    return (
      <div>
        <Editor
          markdownText={this.state.markdownText}
          onUpdate={this.updateMarkdownText}
        />
        <Divider />
        <Preview markdownText={this.state.markdownText} />
      </div>
    );
  }
}
