import './style.css';
import * as React from 'react';
import * as ReactDOM from 'react-dom';
import { Loader } from 'semantic-ui-react';

import { quotes } from './constants';

function getRandomInt(max) {
  return Math.floor(Math.random() * Math.floor(max));
}

class QuoteText extends React.Component {
  render() {
    return <div id="text">
      {this.props.text}
    </div>
  }
}

class AuthorText extends React.Component {
  render() {
    return <div id="author">
      {this.props.text}
    </div>
  }
}

const retrieveQuotes = () => {
  return new Promise((resolve, reject) => {
    setTimeout(() => resolve(quotes), 500);
  })
}

class Buttons extends React.Component {
  render() {
    return <div id="buttons">
      <button className="ui twitter button blue">
        <i className="twitter icon"></i>
      </button>
      <button className="ui tumblr button blue">
        <i className="tumblr icon"></i>
      </button>
      <button className="ui button blue" id="new-quote" onClick={() => this.props.newQuoteFnc()}>
        New Quote
      </button>
    </div>
  }
}

class QuoteBox extends React.Component {
  render() {
    return <div id="quote-box">
      {this.props.quoteText}
      {this.props.authorText}
      {this.props.buttons}
    </div>
  }
}

export default class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      quotes: [],
      choosenQuote: undefined,
      isLoading: true
    };

    this.componentDidMount = this.componentDidMount.bind(this);
    this.pickRandomQuote = this.pickRandomQuote.bind(this);
  }

  componentDidMount() {
    retrieveQuotes()
      .then((response) => {
        this.setState({ quotes: response.quotes, isLoading: false })
        this.pickRandomQuote();
      })
      .catch(function (error) {
        console.log(error);
        this.setState({ isError: true });
      })
  }

  pickRandomQuote() {
    const len = this.state.quotes && this.state.quotes.length || 0;
    console.log("len", len)
    if (len > 0) {
      this.setState({
        quote: this.state.quotes[(getRandomInt(len) - 1)]
      })
    }
  }

  render() {
    return (
      <div style={{ display: "flex", justifyContent: "center", height: "100%", alignItems: "center" }}>
        {
          this.state.isLoading || !this.state.quote
            ? <Loader active />
            : <QuoteBox
              quoteText={<QuoteText text={this.state.quote.quote} />}
              authorText={<AuthorText text={this.state.quote.author} />}
              buttons={<Buttons newQuoteFnc={this.pickRandomQuote} />}
            />
        }
      </div>
    )
  }
}
